

.main-header {
  display: flex;
  margin-top: 13px;
  background-color: $white;
  color: $black;
}

.main-header__container {
  padding-left: 20px;
  padding-right: 20px;
  width: 100%;
}

.main-header-index::after {
  content: "";
  position: absolute;
  right: 0;
  top: 20px;
  min-height: 346px;
  width: 100%;
  background-color: #63a73a;
  background-image: url("../img/index-background-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-position: center top;
  z-index: -1;
}


.main-header__logo {
  display: flex;
  height: 65px;
  text-align: center;
}

.main-header__logo-title {
  margin-left: 20px;
}

.main-header__logo-picture__tablet {
  display: none;
}

.main-header__top {
  display: flex;
  justify-content: space-between;
  margin-bottom: -14px;
}







@media (min-width: $tablet-width) {
  .main-header {
    display: flex;
    margin: 0 auto;
    height: 50px;
    margin-top: 25px;
  }


  .main-header__container {
    padding-left: 30px;
    padding-right: 30px;
    display: flex;
    justify-content: space-between;
  }

  .main-header-index::after {
    display: none;
  }

  .main-header__wrapper {
    padding-left: 30px;
    padding-right: 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .main-header__logo-picture {
    width: 60px;
    height: 50px;
  }



  .main-header__logo-picture__tablet {
    display: block;
  }

  .main-header__top {
    justify-content: flex-start;
    width: 27.11%;
    padding: 0;
  }


}

@media (min-width: $desktop-width) {
  .main-header-index::after {
    display: block;
    content: "";
    position: absolute;
    right: 0;
    min-height: 694px;
    background-color: $green;
    background-image: url("../img/index-background-desktop@1x.jpg");
    background-repeat: no-repeat;
    background-position: right;
    background-size: cover;
    z-index: -1;
    top: -55px;
    width: 50vw;
  }

  .main-header__container {
    width: 100%;
    margin: 0 auto;
    max-width: 1220px;
  }

  .main-header__wrapper {
    padding-left: 0;
    padding-right: 0;
  }

  .main-header {
    position: relative;
    width: 100%;
    background-color: transparent;
    margin-top: 55px;
  }

 
}
