}

.slider-range__togle {
  position: absolute;
  width: 40px;
  height: 40px;
  left: 50%;
  top: 50%;
  transform: translate(-20px, -20px);
  background: #ffffff;
  border: 2px solid #444444;
  border-radius: 20px;
  cursor: pointer;
}
.slider-range__togle:hover {
  border: 2px solid #68b738;
