  border: 2px solid #68b738;
}
.slider-range__togle--left::before, .slider-range__togle--right::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-4px, -9px);
  margin-left: -9px;
  border-top: 9px solid transparent;
  border-bottom: 9px solid transparent;
  border-right: 9px solid #444444;
}
.slider-range__togle--right::after {
  transform: rotate(180deg) translate(-13px, 9px);
}
.slider-range__togle--left:hover::before, .slider-range__togle--right:hover::after {
  border-right-color: #68b738;
}
